const db = [
  { id: '1', name: ' 1', address: ' 1518 ' },
  { id: '2', name: ' 2', address: ' 1517 ' },
  { id: '3', name: ' 3', address: ' 1519 ' },
  { id: '4', name: ' 4', address: ' 1516 ' }
]

export default [
  {
    path: '/api/demo/business/issues/142/fetch.*',
    method: 'get',
    handle () {
      return {
        code: 0,
        msg: '',
        data: {
          list: db
        }
      }
    }
  },
  {
    path: '/api/demo/business/issues/142/detail.*',
    method: 'get',
    handle ({ params }) {
      return {
        code: 0,
        msg: '',
        data: db.find(e => e.id === params.id)
      }
    }
  }
]
