import Vue from 'vue'
import router from '@/router'

export default {
  namespaced: true,
  state: {
    // 
    value: '' // medium small mini
  },
  actions: {
    /**
     * @description  element
     * @param {Boolean} refresh 
     */
    apply ({ state, commit }, refresh) {
      Vue.prototype.$ELEMENT.size = state.value
      if (refresh) {
        commit('d2admin/page/keepAliveClean', null, { root: true })
        router.replace('/refresh')
      }
    },
    /**
     * @description  https://github.com/d2-projects/d2-admin/issues/198
     */
    isLoaded ({ state }) {
      if (state.value) return Promise.resolve()
      return new Promise(resolve => {
        const timer = setInterval(() => {
          if (state.value) {
            resolve(clearInterval(timer))
          }
        }, 10)
      })
    },
    /**
     * @description 
     * @param {Object} state vuex state
     * @param {String} size 
     */
    set ({ state, dispatch }, size) {
      return new Promise(async resolve => {
        // store 
        state.value = size
        // 
        dispatch('apply', true)
        // 
        await dispatch('d2admin/db/set', {
          dbName: 'sys',
          path: 'size.value',
          value: state.value,
          user: true
        }, { root: true })
        // end
        resolve()
      })
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    load ({ state, dispatch }) {
      return new Promise(async resolve => {
        // store 
        state.value = await dispatch('d2admin/db/get', {
          dbName: 'sys',
          path: 'size.value',
          defaultValue: 'default',
          user: true
        }, { root: true })
        // 
        dispatch('apply')
        // end
        resolve()
      })
    }
  }
}
