import util from '@/libs/util'
import router from '@/router'
import { cloneDeep } from 'lodash'

/**
 * @description  
 * @param {Object} param dbName {String} 
 * @param {Object} param path {String} 
 * @param {Object} param user {Boolean} 
 * @param {Object} param validator {Function}   true 
 * @param {Object} param defaultValue {*} 
 * @returns {String} 
 */
function pathInit ({
  dbName = 'database',
  path = '',
  user = true,
  validator = () => true,
  defaultValue = ''
}) {
  const uuid = util.cookies.get('uuid') || 'ghost-uuid'
  const currentPath = `${dbName}.${user ? `user.${uuid}` : 'public'}${path ? `.${path}` : ''}`
  const value = util.db.get(currentPath).value()
  if (!(value !== undefined && validator(value))) {
    util.db
      .set(currentPath, defaultValue)
      .write()
  }
  return currentPath
}

export default {
  namespaced: true,
  actions: {
    /**
     * @description  | 
     * @description  dbName.path = value
     * @param {Object} param dbName {String} 
     * @param {Object} param path {String} 
     * @param {Object} param value {*} 
     * @param {Object} param user {Boolean} 
     */
    set (context, {
      dbName = 'database',
      path = '',
      value = '',
      user = false
    }) {
      util.db.set(pathInit({
        dbName,
        path,
        user
      }), value).write()
    },
    /**
     * @description 
     * @description  dbName.path || defaultValue
     * @param {Object} param dbName {String} 
     * @param {Object} param path {String} 
     * @param {Object} param defaultValue {*} 
     * @param {Object} param user {Boolean} 
     */
    get (context, {
      dbName = 'database',
      path = '',
      defaultValue = '',
      user = false
    }) {
      return new Promise(resolve => {
        resolve(cloneDeep(util.db.get(pathInit({
          dbName,
          path,
          user,
          defaultValue
        })).value()))
      })
    },
    /**
     * @description 
     * @param {Object} context context
     * @param {Object} param user {Boolean} 
     */
    database (context, {
      user = false
    } = {}) {
      return new Promise(resolve => {
        resolve(util.db.get(pathInit({
          dbName: 'database',
          path: '',
          user,
          defaultValue: {}
        })))
      })
    },
    /**
     * @description 
     * @param {Object} context context
     * @param {Object} param user {Boolean} 
     */
    databaseClear (context, {
      user = false
    } = {}) {
      return new Promise(resolve => {
        resolve(util.db.get(pathInit({
          dbName: 'database',
          path: '',
          user,
          validator: () => false,
          defaultValue: {}
        })))
      })
    },
    /**
     * @description  [  ]
     * @param {Object} context context
     * @param {Object} param basis {String}  [ name | path | fullPath ]
     * @param {Object} param user {Boolean} 
     */
    databasePage (context, {
      basis = 'fullPath',
      user = false
    } = {}) {
      return new Promise(resolve => {
        resolve(util.db.get(pathInit({
          dbName: 'database',
          path: `$page.${router.app.$route[basis]}`,
          user,
          defaultValue: {}
        })))
      })
    },
    /**
     * @description  [  ]
     * @param {Object} context context
     * @param {Object} param basis {String}  [ name | path | fullPath ]
     * @param {Object} param user {Boolean} 
     */
    databasePageClear (context, {
      basis = 'fullPath',
      user = false
    } = {}) {
      return new Promise(resolve => {
        resolve(util.db.get(pathInit({
          dbName: 'database',
          path: `$page.${router.app.$route[basis]}`,
          user,
          validator: () => false,
          defaultValue: {}
        })))
      })
    },
    /**
     * @description  ( $data ) 
     * @param {Object} context context
     * @param {Object} param instance {Object} vue 
     * @param {Object} param basis {String}  [ name | path | fullPath ]
     * @param {Object} param user {Boolean} 
     */
    pageSet (context, {
      instance,
      basis = 'fullPath',
      user = false
    }) {
      return new Promise(resolve => {
        resolve(util.db.get(pathInit({
          dbName: 'database',
          path: `$page.${router.app.$route[basis]}.$data`,
          user,
          validator: () => false,
          defaultValue: cloneDeep(instance.$data)
        })))
      })
    },
    /**
     * @description 
     * @param {Object} context context
     * @param {Object} param instance {Object} vue 
     * @param {Object} param basis {String}  [ name | path | fullPath ]
     * @param {Object} param user {Boolean} 
     */
    pageGet (context, {
      instance,
      basis = 'fullPath',
      user = false
    }) {
      return new Promise(resolve => {
        resolve(cloneDeep(util.db.get(pathInit({
          dbName: 'database',
          path: `$page.${router.app.$route[basis]}.$data`,
          user,
          defaultValue: cloneDeep(instance.$data)
        })).value()))
      })
    },
    /**
     * @description 
     * @param {Object} context context
     * @param {Object} param basis {String}  [ name | path | fullPath ]
     * @param {Object} param user {Boolean} 
     */
    pageClear (context, {
      basis = 'fullPath',
      user = false
    }) {
      return new Promise(resolve => {
        resolve(util.db.get(pathInit({
          dbName: 'database',
          path: `$page.${router.app.$route[basis]}.$data`,
          user,
          validator: () => false,
          defaultValue: {}
        })))
      })
    }
  }
}
