import dayjs from 'dayjs'
import { get } from 'lodash'
import util from '@/libs/util.js'

export default {
  namespaced: true,
  state: {
    // 
    // + 
    //   - message  
    //   - type   success | warning | info() | danger
    //   - time  
    //   - meta  
    log: []
  },
  getters: {
    /**
     * @description  log (all) 
     * @param {*} state vuex state
     */
    length (state) {
      return state.log.length
    },
    /**
     * @description  log (error) 
     * @param {*} state vuex state
     */
    lengthError (state) {
      return state.log.filter(log => log.type === 'danger').length
    }
  },
  actions: {
    /**
     * @description 
     * @param {String} param message {String} 
     * @param {String} param type {String} 
     * @param {Object} param meta {Object} 
     */
    push ({ rootState, commit }, { message, type = 'info', meta }) {
      commit('push', {
        message,
        type,
        time: dayjs().format('YYYY-MM-DD HH:mm:ss'),
        meta: {
          // 
          user: rootState.d2admin.user.info,
          //  uuid
          uuid: util.cookies.get('uuid'),
          //  token
          token: util.cookies.get('token'),
          // 
          url: get(window, 'location.href', ''),
          // 
          ...meta
        }
      })
    }
  },
  mutations: {
    /**
     * @description 
     * @param {Object} state vuex state
     * @param {Object} log data
     */
    push (state, log) {
      state.log.push(log)
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    clean (state) {
      // store 
      state.log = []
    }
  }
}
