import store from '@/store'
import axios from 'axios'
import { Message } from 'element-ui'
import util from '@/libs/util'

// 
function errorCreate (msg) {
  const error = new Error(msg)
  errorLog(error)
  throw error
}

// 
function errorLog (error) {
  // 
  store.dispatch('d2admin/log/push', {
    message: '',
    type: 'danger',
    meta: {
      error
    }
  })
  // 
  if (process.env.NODE_ENV === 'development') {
    util.log.danger('>>>>>> Error >>>>>>')
    console.log(error)
  }
  // 
  Message({
    message: error.message,
    type: 'error',
    duration: 5 * 1000
  })
}

//  axios 
const service = axios.create({
  baseURL: process.env.VUE_APP_API,
  timeout: 5000 // 
})

// 
service.interceptors.request.use(
  config => {
    // 
    const token = util.cookies.get('token')
    // token-- ['X-Token']key 
    config.headers['X-Token'] = token
    return config
  },
  error => {
    // 
    console.log(error)
    Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  response => {
    // dataAxios  axios  data
    const dataAxios = response.data
    // 
    const { code } = dataAxios
    //  code 
    if (code === undefined) {
      //  code   D2Admin 
      return dataAxios
    } else {
      //  code  
      switch (code) {
        case 0:
          // [  ] code === 0 
          return dataAxios.data
        case 'xxx':
          // [  ]  code
          errorCreate(`[ code: xxx ] ${dataAxios.msg}: ${response.config.url}`)
          break
        default:
          //  code
          errorCreate(`${dataAxios.msg}: ${response.config.url}`)
          break
      }
    }
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400: error.message = ''; break
        case 401: error.message = ''; break
        case 403: error.message = ''; break
        case 404: error.message = `: ${error.response.config.url}`; break
        case 408: error.message = ''; break
        case 500: error.message = ''; break
        case 501: error.message = ''; break
        case 502: error.message = ''; break
        case 503: error.message = ''; break
        case 504: error.message = ''; break
        case 505: error.message = 'HTTP'; break
        default: break
      }
    }
    errorLog(error)
    return Promise.reject(error)
  }
)

export default service
