import store from '@/store'
import util from '@/libs/util'

export default {
  install (Vue, options) {
    //  log
    Vue.prototype.$log = {
      ...util.log,
      push (data) {
        if (typeof data === 'string') {
          // 
          //  message 
          // 
          // eg: this.$log.push('foo text')
          store.dispatch('d2admin/log/push', {
            message: data
          })
        } else if (typeof data === 'object') {
          // 
          store.dispatch('d2admin/log/push', data)
        }
      }
    }
  }
}
